    height: 400px;
    border-right: 1px solid #efe8ed; }
    .profile .profile-sidebar ul {
      margin-top: 30px; }
    .profile .profile-sidebar ul .current {
      width: 92%;
      margin: 0;
      border-right-color: #fff;
      background: #f1f3f5;
      -webkit-transition: background 500ms ease-in-out;
      -o-transition: background 500ms ease-in-out;
      transition: background 500ms ease-in-out; }
    .profile .profile-sidebar ul li {
      line-height: 30px;
      padding: 9px;
      cursor: pointer; }
  .profile .profile-content {
    width: 100%;
    height: 100%;
    margin-left: 15%;
    padding: 10px; }
    .profile .profile-content .info .div {
      margin: 20px;
