@media (max-width: 414px) {
  .profile > div:first-child {
    width: 25%; }
    .profile > div:first-child ul .current {
      width: 85%; }
  .profile > div:last-child {
    width: 80%;
    height: 100%;
    margin-left: 25%; }
    .profile > div:last-child .integrations {
      -ms-flex-direction: column;
          flex-direction: column; }
      .profile > div:last-child .integrations div {
        width: 95%; }
    .profile > div:last-child .settings div {
      width: 85%; }
