header div {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100%; }

header {
  padding: 20px;
  border: 1px solid #efe8ed;
  position: fixed;
  background: #0097A7;
  top: 0;
  z-index: 2;
  width: 100%; }
  header section {
    max-width: 1200px;
    margin: 0 auto; }
  header div {
    background-image: url("../../images/logo.png");
    border-radius: 50%;
    width: 70px;
    height: 70px; }
  header h1 {
    color: #555; }
  header ul {
    margin-right: 30px; }
  header ul li {
    margin-left: 20px;
    color: #555;
    list-style: none;
    cursor: pointer;
    font-size: 16px; }
  header a {
    text-decoration: none;
    color: #555; }

@media (max-width: 414px) {
  header {
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center; }
    header h1 {
      margin-bottom: 10px; } }



