.loader {
  width: 160px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around; }

.loader.loader--center {
  margin: auto; }

.loader .loader__item {
  display: inline-block;
  background-color: #FFFFFF;
  width: 50px;
  height: 50px;
  border-radius: 50%; }

.loader .loader__item:nth-child(1) {
  -webkit-animation: loader 1s ease-in-out 0s infinite alternate;
          animation: loader 1s ease-in-out 0s infinite alternate; }

.loader .loader__item:nth-child(2) {
  -webkit-animation: loader 1s ease-in-out 0.2s infinite alternate;
          animation: loader 1s ease-in-out 0.2s infinite alternate; }

.loader .loader__item:nth-child(3) {
  -webkit-animation: loader 1s ease-in-out 0.4s infinite alternate;
          animation: loader 1s ease-in-out 0.4s infinite alternate; }

@-webkit-keyframes loader {
  0% {
    background-color: #9055A2;
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background: #D499B9; } }

@keyframes loader {
  0% {
    background-color: #9055A2;
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background: #D499B9; } }



