    padding: 20px;
    overflow: hidden;
    margin: 20px;
    width: 280px;
    height: 280px;
    opacity: 0;
    background: #fff;
    -webkit-box-shadow: 3px 7px 10px 2px #efe8ed;
            box-shadow: 3px 7px 10px 2px #efe8ed;
    -webkit-animation: fadein 500ms ease-in-out forwards;
            animation: fadein 500ms ease-in-out forwards; }
    .search_events .week img {
      width: 100%; }
    .search_events .week .icons {
      position: absolute;
      bottom: 20px;
      right: 15px;
      font-size: 18px;
      opacity: 0; }
      .search_events .week .icons .saved {
        color: #0938f5;
        opacity: 1; }
      .search_events .week .icons i {
        margin-left: 10px;
